import React, { useEffect } from 'react';
import { ConfirmModal, FileUploader, Loader } from 'shared/components';
import clsx from 'clsx';
import CloseIcon from '@material-ui/icons/Close';
import IconButton from '@material-ui/core/IconButton';
import { FormikSetFieldValueType } from 'shared/typings';
import { useBusinessProfile } from '../hooks';
import DeleteIcon from '@material-ui/icons/Delete';
import { useWhiteModal } from 'shared/hooks';
import { useAppState } from 'shared/state';
import { useTranslation } from 'react-i18next';

export type BpImageUploaderType = {
	field: 'icon' | 'logo' | 'icon_dark' | 'logo_dark';
	setFieldValue: FormikSetFieldValueType;
};

export function BpImageUploader({ field, setFieldValue }: BpImageUploaderType) {
	const {
		uploadMeta,
		setUploadMeta,
		cancelSending,
		setCancelSending,
		setFiles,
		files,
		deleteBusinessProfileAvatar,
		isDeleteBusinessAvatarRequestLoading
	} = useBusinessProfile();
	const { isAdmin } = useAppState();
	const { t } = useTranslation();

	const { open: isShowDeleteAvatarModal, modalHandler: toggleDeleteAvatarModal } = useWhiteModal();

	function handleDeleteAvatarConfirmPressed() {
		deleteBusinessProfileAvatar(field);
		toggleDeleteAvatarModal();
	}

	useEffect(() => {
		if (files[field]) {
			setFieldValue(field, files[field]);
		}
	}, [files]);

	const isLogoField = field.includes('logo');

	return (
		<div className="lg:flex">
			<FileUploader
				uploadMeta={uploadMeta}
				setUploadMeta={setUploadMeta}
				cancelSending={cancelSending}
				setCancelSending={setCancelSending}
				setFile={setFiles}
				isBusinessProfile
				field={field}
			/>
			<div className="flex-grow-1 mt-4 lg:mx-6 lg:mt-0">
				<div
					className={clsx('group relative overflow-hidden border border-kgrey-border-solid ', {
						'h-24 w-24 rounded-full': !isLogoField,
						'h-16 w-64 rounded-md': isLogoField
					})}
				>
					<img
						src={
							files?.[field]?.url ||
							(field === 'icon'
								? `https://via.placeholder.com/96x96/`
								: 'https://via.placeholder.com/263x60/')
						}
						alt={field}
						className="h-full w-full object-cover"
					/>
					{uploadMeta.progress !== null && uploadMeta.field === field && (
						<>
							<div className="position-centered absolute  z-10">
								<Loader variant="static" value={uploadMeta.progress} />
							</div>
							<IconButton
								disabled={isDeleteBusinessAvatarRequestLoading}
								onClick={() => {
									setCancelSending(true);
									setUploadMeta((prevState) => ({ ...prevState, progress: null }));
								}}
								className="position-centered !absolute z-20 text-white"
							>
								<CloseIcon className="text-primary" />
							</IconButton>
						</>
					)}
					{files[field] ? (
						<div className="absolute bottom-0 left-0 hidden h-1/3 w-full group-hover:block group-hover:bg-kgrey group-hover:bg-opacity-50 ">
							<IconButton
								className={clsx('absolute top-1/4 left-1/4 h-full', {
									'!top-0 !left-[40%]': isLogoField
								})}
								disabled={!isAdmin}
								onClick={toggleDeleteAvatarModal}
							>
								<DeleteIcon className="text-red-400" />
							</IconButton>
						</div>
					) : null}
				</div>
				<div className="mt-1 text-xs text-kgrey">
					{field.includes('icon')
						? t('please_use_fit_image')
						: t('please_use_following_dimensioned_image')}
				</div>
				<ConfirmModal
					toggleHandler={toggleDeleteAvatarModal}
					isOpen={isShowDeleteAvatarModal}
					featureHandler={handleDeleteAvatarConfirmPressed}
					title={`Are you sure to delete the business profile's ${isLogoField ? 'logo' : 'icon'} `}
					isLoading={isDeleteBusinessAvatarRequestLoading}
				/>
				{/* ignore this element, this is for safelist-classNames: read more here: https://tailwindcss.com/docs/content-configuration#safelisting-classes*/}
				<div className="safelist-classNames !top-[20%] !left-[40%] hidden"></div>
			</div>
		</div>
	);
}
