import React from 'react';
import EntityForm from 'modules/entity/forms';
import isEmpty from 'lodash/isEmpty';
import { useAuth } from 'modules/auth/hooks/useAuth';
import { storage } from 'shared/services';
import { useDispatch } from 'react-redux';
import actions from 'store/actions';
import { useBusinessProfile } from './hooks';
import { BpFormElements } from './components';
import { useNotistack } from 'shared/hooks';
import { useAsStaff } from '../../../BusinessManagement/hooks/useAsStaff';
import { useTranslation } from 'react-i18next';

const { setUserCompany } = actions.user;

export function BusinessProfile() {
	const { t } = useTranslation();
	const { company } = useAuth();
	const { files, selected, placeAddress, calcedLat, calcedLng, companyId } =
		useBusinessProfile(true);
	const { showNotification } = useNotistack();
	const dispatch = useDispatch();
	const { loadAllBusiness } = useAsStaff();

	return (
		<div className="business-profile">
			{company ? (
				<>
					<EntityForm.Main
						entity="profile"
						name="business-profile"
						url={`/${companyId}/my/company/`}
						method="patch"
						sendAsFormData={false}
						normalizeData=""
						prependData
						fields={[
							{
								name: 'name',
								value: company.company.name || '',
								onSubmitKey: ''
							},
							{
								name: 'status',
								value: 'ACTIVE',
								onSubmitKey: ''
							},
							{
								name: 'created_date',
								value: company.company.created_date,
								onSubmitKey: ''
							},
							{
								name: 'website',
								value: company.company.website || '',
								type: 'website',
								onSubmitKey: ''
							},
							{
								name: 'city',
								value: (!isEmpty(placeAddress) ? placeAddress.city : company.company.city) || '',
								onSubmitKey: ''
							},
							{
								name: 'address',
								value:
									(!isEmpty(placeAddress) ? placeAddress.plain() : company.company.address) || '',
								onSubmitKey: ''
							},
							{
								name: 'address_2',
								value: company.company.address_2 || '',
								onSubmitKey: ''
							},
							{
								name: 'zipcode',
								value: (!isEmpty(placeAddress) ? placeAddress.zip : company.company.zipcode) || '',
								onSubmitKey: ''
							},
							{
								name: 'country',
								value:
									(!isEmpty(placeAddress)
										? placeAddress.country
										: typeof company.company.country === 'object' &&
										  company.company.country !== null
										? company.company.country?.name
										: company.company.country) || '',
								required: true,
								onSubmitKey: ''
							},
							{
								name: 'state',
								value:
									(!isEmpty(placeAddress)
										? placeAddress.state
										: typeof company.company.state === 'object' && company.company.state !== null
										? company.company.state?.name
										: company.company.state) || '',
								required: true,
								onSubmitKey: ''
							},
							{
								name: 'phone',
								value: company.company.phone || '',
								onSubmitKey: ''
							},
							{
								name: 'about',
								value: company.company.about || '',
								onSubmitKey: ''
							},
							{
								name: 'industry',
								value: company.company.industry,
								onSubmitKey: '',
								type: 'object',
								required: true
							},
							{
								name: 'sub_industry',
								value: company.company.sub_industry,
								onSubmitKey: '',
								type: 'object',
								required: false
							},
							{
								name: 'icon',
								value: files.icon,
								onSubmitKey: '',
								type: 'object'
							},
							{
								name: 'icon_dark',
								value: files.icon_dark,
								onSubmitKey: '',
								type: 'object'
							},
							{
								name: 'logo',
								value: files.logo,
								onSubmitKey: '',
								type: 'object'
							},
							{
								name: 'logo_dark',
								value: files.logo_dark,
								onSubmitKey: '',
								type: 'object'
							},
							{
								name: 'facebook_url',
								value: company.company.facebook_url,
								onSubmitKey: '',
								type: 'website'
							},
							{
								name: 'twitter_url',
								value: company.company.twitter_url || '',
								onSubmitKey: '',
								type: 'website'
							},
							{
								name: 'lat',
								// value: Number(marker.lat).toFixed(9) || ""
								value: (selected ? Number(selected.lat).toFixed(9) : String(calcedLat)) || '',
								onSubmitKey: ''
							},
							{
								name: 'lng',
								// value: Number(marker.lng).toFixed(9) || ""
								value: (selected ? Number(selected.lng).toFixed(9) : String(calcedLng)) || '',
								onSubmitKey: ''
							}
						]}
						onSuccess={(response, resetForm): any => {
							const companyPayload = {
								...company,
								company: { ...response, owner: company?.company?.owner || {} }
							};
							storage.set('company', JSON.stringify(companyPayload));
							dispatch(setUserCompany(companyPayload));
							loadAllBusiness();
							showNotification({ message: t('update_success'), variant: 'success' });
						}}
						onError={(error, resetForm): any => {
							resetForm();

							let errorMessage = t('something_went_wrong');

							if (error.response && error.response?.data) {
								const message = error.response?.data?.message;

								if (message.match('Company already Exists!')) {
									errorMessage = 'The Business Name Already Exists!';
								} else {
									errorMessage = message;
								}
							}
							showNotification({ message: errorMessage, variant: 'error' });
						}}
					>
						{({ isSubmitting, values, setFieldValue, errors, setFieldError }) => {
							// console.log('business profile edit',values)
							return (
								<BpFormElements
									values={values}
									setFieldValue={setFieldValue}
									isSubmitting={isSubmitting}
									errors={errors}
									setFieldError={setFieldError}
								/>
							);
						}}
					</EntityForm.Main>{' '}
				</>
			) : null}
		</div>
	);
}
